// init Swiper.js

import Swiper from 'swiper/bundle';

var mySwiper = new Swiper('.swiper-container', {
    pagination: {
        el: '.swiper-pagination',
        clickable: true,
    },
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
    },
    on: {
        slideChangeTransitionStart() {
            if ($('#a-market-slide').hasClass('swiper-slide-active')) {
                $('#a-market').addClass('active');
            } else {
                $('#a-market').removeClass('active');
            }

            if ($('#techport-slide').hasClass('swiper-slide-active')) {
                $('#techport').addClass('active');
            } else {
                $('#techport').removeClass('active');
            }

            if ($('#kolesa-darom-slide').hasClass('swiper-slide-active')) {
                $('#kolesa-darom').addClass('active');
            } else {
                $('#kolesa-darom').removeClass('active');
            }
        }
    }
});

// additional scripts

$(function(){
    var mySwiper = document.querySelector('.swiper-container').swiper;


    const partnersList = $('.product-partners__list');
    const partners = [
        'index-iq', 'yds', 'siberia','global-drive','kirgu',
        'karolina','electrobyt','zeo','atlanta','fuji','100pechei','avtogik',
        'kolesa-darom','techport','a-market','electropark'
    ];

    partners.forEach((item) => {
        partnersList.append(`<img class="product-partners__img" id="${item}" src="/src/assets/images/${item}.svg">`);
    });
    $('.product-btn__orange').on('click', function(e){
        $('html,body').stop().animate({ scrollTop: $('#product-sale').offset().top }, 1000);
        e.preventDefault();
    });

    let question = $(".question-title-wrapper");

    question.click(function () {
        $(this).toggleClass('expanded');
    });

    $('#a-market').addClass('active');


    let variantOne = $('#variantOne');
    let variantTwo = $('#variantTwo');
    let variantThree = $('#variantThree');

    let image = $('.product-variant__image');

    let variants = [variantOne, variantTwo, variantThree];

    variantOne.click(function () {
        $.each(variants, function (index, value) {
            if (value.hasClass('selected')) {
                value.removeClass('selected')
            }
        });
        $(this).addClass('selected');
        image.addClass('variantOne');
        if (image.hasClass('variantTwo') || image.hasClass('variantThree')) {
            image.removeClass().addClass('product-variant__image variantOne');
        }
    });

    variantTwo.click(function () {
        $.each(variants, function (index, value) {
            if (value.hasClass('selected')) {
                value.removeClass('selected')
            }
        });
        $(this).addClass('selected');
        image.addClass('variantTwo');
        if (image.hasClass('variantOne') || image.hasClass('variantThree')) {
            image.removeClass().addClass('product-variant__image variantTwo');
        }
    });

    variantThree.click(function () {
        $.each(variants, function (index, value) {
            if (value.hasClass('selected')) {
                value.removeClass('selected')
            }
        });
        $(this).addClass('selected');
        image.addClass('variantThree');
        if (image.hasClass('variantOne') || image.hasClass('variantTwo')) {
            image.removeClass().addClass('product-variant__image variantThree');
        }
    });
});